const reducer = (state: any, action: any) => {

    switch (action.type) {

        case 'setActive':

            const id = action.id;
            const projects = state.projects.map((pr:any)=>{

                if (pr.name === id) {

                    const prr = { ...pr };
                    prr.active = true;
                    return prr;

                } else {

                    if (pr.active === true) {

                        const prr = { ...pr };
                        prr.active = false;
                        return prr;

                    } else {

                        return pr;

                    }

                }


            });

        return { ...state, projects: projects }
        default:
            return state;

    }

}

interface WebProjectsProps {



}
 
const WebProjects: React.FC<WebProjectsProps> = () => {

    document.title = "Mirsad Avdic | Web Projects";
    const [ state, dispatch ] = useReducer(reducer, initialState);
 
    return (<WPContainer>
        <ProjectsSidebar />
        <Projects state={state} dispatch={dispatch} />
    </WPContainer>);


}

export default WebProjects;